export default {
	data() {
		return {
			textList: ['财源滚滚', '步步高升', '一夜暴富', '每赌必赢', '逢凶化吉', '业绩长虹', '生意兴隆', '贵人相助'],
			selIdList: [],
			hasToken: false
		}
	},
	onLoad() {

	},
	onShow() {
		this.hasToken = uni.getStorageSync('token') ? true : false
	},
	methods: {
		goMatch() {
			try {
				let wishList = []
				if (this.selIdList.length === 3) {
					this.selIdList.forEach((item) => {
						wishList.push(this.textList[item])
					})
				} else {
					uni.showToast({
						title: "以上限求三项",
						icon: 'none'
					})
					return
				}
				console.log(wishList)
				uni.setStorage({
					key: 'wishList',
					data: wishList,
					success: function() {
						uni.navigateTo({
							url: '/pages/birthday/birthday'
						});
					}
				});
			} catch (e) {
				// error
			}
		},
		getUserInfo(e) {
			console.log(e)
			wx.login({
				success: (zz) => {
					console.log(zz);
					wx.getUserInfo({
						success: (res) => {
							console.log(res);
								this.$api.post('/api/user/login', {
									code: zz.code,
									avatar_url: res.userInfo.avatarUrl,
									nickname: res.userInfo.nickName,
									iv: e.detail.iv,
									encryptedData: e.detail.encryptedData
								}).then((res1) => {
									console.log(res1)
									if (res1.access_token) {
										this.hasToken = true
									} else {
										return
									}
									uni.setStorageSync('token', res1.access_token)
									uni.setStorageSync('user_id', res1.user_id)
									this.goMatch()
									// uni.setStorage({
									//     key: 'user_id',
									//     data: res.user_id,
									//     success:()=>{
									// 		console.log('user_id set success')
									//     }
									// });
								})
						},
						complete:(e)=>{
							console.log(e)
						}
					});
				}
			});
		},
		selItem(index) {
			let i = this.selIdList.indexOf(index)
			if (i > -1) {
				this.selIdList.splice(i, 1)
			} else {
				if (this.selIdList.length >= 3) {
					this.selIdList.shift()
					this.selIdList.push(index)
				} else {
					this.selIdList.push(index)
				}
			}
		}
	}
}
